.hero {
    width: 100vw;
    display: flex;
    flex-direction: column;

    &--title-right {
        position: relative;
        top: 20px;
        right: 40px;
        width: 70%;
        text-align: right;
        align-self: flex-end;
        word-wrap: break-word;
    }

    &--title-left {
        position: relative;
        left: 20px;
        width: 100%;
        height: 260px;
        text-align: left;
        align-self: flex-start;
    }

    .hero-img {
        position: relative;
        width: 100%;
        
        display: flex;
        flex-direction: row;
        gap: 30px;
        padding: 0 30px;
        justify-content: space-between;

        img {
            position: relative;
            height: 300px;
            width: 20%;
            object-fit: cover;
        }
    }
}

